import React, {useState,useEffect} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Button, Card, Table,Badge} from 'antd'
import {_getAllCategory} from "../../../redux/actions";
import Flex from "../Flex";
import {ReloadOutlined,EyeOutlined} from "@ant-design/icons";
import {Link} from "react-router-dom";
import moment from "moment";
const CategoryTable = (props)=> {
    let { card,config, _getAllCategory, lists } = props

    const columns = [
        {
            key:"ID",
            title:"No",
            width:50,
            render:(_,val,index)=> index + 1
        },
        {
            dataIndex:"name",
            key:"name",
            title:"Name",
            render: (_,val)=> {
                return <Badge color={val?.background} text={_}/>
            }
        },
        {
            dataIndex:"slug",
            key:"slug",
            title:"Slug",
        },
        {
            dataIndex:"createdAt",
            width:260,
            key:"createdAt",
            title:"Created At",
            render:(_)=> moment(_).format("DD MMMM YYYY HH:mm:ss a")
        },

        {
            key:"action",
            title:"",
            width:60,
            render:(_,val)=> {
                return (
                    <div className="text-right">
                        <Link to={`/app/travel/category/p/${_?.slug}`}>
                            <Button size={'small'} icon={<EyeOutlined/>}/>
                        </Link>
                    </div>
                )
            }
        }

    ]
    const data = []

    const ExtraComponent= ()=> {
        return (
            <Flex alignItems={'center'} mobileFlex={true}>
                <Button size={'small'} icon={<ReloadOutlined/>}/>
            </Flex>
        )
    }


    useEffect(()=> {
        _getAllCategory({...config?.params})
    },[_getAllCategory])


    return (
        <Card {...card} loading={card?.loading ?? lists?.loading ?? false} extra={card?.extra ?? <ExtraComponent/>}>
            <Table
                dataSource={lists?.data ?? []}
                columns={columns}

            />
        </Card>
    )
}

CategoryTable.propTypes = {
    card: PropTypes.shape({
        title: PropTypes.string,
        bordered: PropTypes.bool,
        extra: PropTypes.any
    }).isRequired,
    config: PropTypes.shape({
        params: PropTypes.shape({
            page:PropTypes.number,
            limit:PropTypes.number
        }),
    }),
    _getAllCategory: PropTypes.func.isRequired
}
CategoryTable.defaultProps = {
    card: {
        bordered:false
    },
    config:{
        params:{
            page:1,
            limit:10
        }
    }
}

export default connect(({Travel})=> {
    let { categories } = Travel
    return {...categories}
},{
    _getAllCategory
})(React.memo(CategoryTable))
