import React, {useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {_adminGetTravelCategoryList} from "../../../../redux/actions";
import CategoryTable from "../../../../components/shared-components/category-list/category-table";

const TravelCategory = (props)=> {
    let { _adminGetTravelCategoryList } = props

    return (
        <CategoryTable card={{title:"Category",bordered:false}}/>
    )
}

TravelCategory.propTypes = {
    _adminGetTravelCategoryList: PropTypes.func.isRequired
}
TravelCategory.defaultProps = {
    _adminGetTravelCategoryList: (query = {})=> {
        console.log(query)
    }
}
export default connect(({Travel})=> {
    let { admin } = Travel
    let { category } = admin
    return {...category}
},{_adminGetTravelCategoryList})(React.memo(TravelCategory))
